.VideoListItemContainer {
  margin-bottom: 30px;

  border: 1px solid #f0932b;
  border-radius: 3px;

  padding: 15px;

  cursor: pointer;

  -webkit-transition: -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);

  transition: -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);

  -o-transition: transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);

  transition: transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);

  transition: transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1), -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.VideoListItemContainer:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.VideoListItemContainer > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.VideoImage {
  margin-right: 30px;

  height: 120px;

  -webkit-box-shadow: 0px 10px 30px -10px rgba(0,0,0,0.75);
  box-shadow: 0px 10px 30px -10px rgba(0,0,0,0.75);
}

.VideoTitle {
  font-size: 1.3rem;

  color: #4834d4;
}


