.VideoDetailContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;

  margin-bottom: 30px;
  margin-right: 30px;
}

.VideoFrame {
  margin-right: 30px;
}

.VideoTextContainer {

}

.VideoTextContainer > h1 {
  font-weight: 900;
  
  color: #4834d4;

  margin-bottom: 10px;
}

.VideoTextContainer > p {
  font-size: 1rem;
  font-weight: 400;

  color: #130f40;
}

.Iframe {
  height: 300px;
  width: 500px;

  border: 0;
  border-radius: 3px;

  margin-bottom: 30px;

  -webkit-box-shadow: 0px 15px 30px -10px rgba(0,0,0,0.75);
  box-shadow: 0px 15px 30px -10px rgba(0,0,0,0.75);

  -webkit-transition: 
    -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    -webkit-box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  transition: 
    -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    -webkit-box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  -o-transition: 
    transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  transition: 
    transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);

  transition: 
    transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
    -webkit-transform 300ms cubic-bezier(0.075, 0.82, 0.165, 1),
    -webkit-box-shadow 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.Iframe:hover {
  -webkit-transform: scale(1.03);
      -ms-transform: scale(1.03);
          transform: scale(1.03);

  -webkit-box-shadow: 0px 12px 30px -10px rgba(0,0,0,0.75);
  box-shadow: 0px 12px 30px -10px rgba(0,0,0,0.75);
}


